import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';
import { PARTTERNS } from '../../constant/props';

class Warpper extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			partten,
			advanced,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'warpper',
			{
				['advanced']:advanced,
				['warpper--' + partten]:partten,
			},
			className
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				<div className='warpper-content clearfix'>
					{children}
				</div>
			</div>
		);
	}
};

Warpper.propTypes = {
	partten: PropTypes.oneOf(PARTTERNS),
	advanced: PropTypes.bool,
	className: PropTypes.string,
	children: PropTypes.node,
};

Warpper.defaultProps = {
	advanced: false,
};

export default Warpper;